









































import { Component, Vue } from 'vue-property-decorator';
import { namespace } from 'vuex-class';

export interface BookData {
  img: any;
  name: string;
  price: string;
  publishing: string;
  desc: string;
  isbn: number;
  status: boolean;
}

const someModule = namespace('book');
@Component({
  components: {},
})
export default class Home extends Vue {
  // @Action('book/getQueryBookList') private getQueryBookList: any;
  // @Getter('book/bookList') bookList!: any;
  @someModule.State((state) => state.bookList) public bookList: any;
  @someModule.State((state) => state.loading) public loading: any;
  @someModule.Action('getQueryBookList') public getQueryBookList: any;

  private activeName: string = 'first';
  private isbm: string = '';


  public handleBookClick() {
    this.getQueryBookList({ isbn: this.isbm });
  }

}
